.backdrop {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  z-index: 9998;
  background: rgba(0, 0, 0, 0.8);
}

.modal {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 9999;
  padding: 1rem 2rem;
  min-width: 20rem;
  color: #222;
  background: #fff;
  -webkit-box-shadow: 0px 0px 3px #000;
          box-shadow: 0px 0px 3px #000;
}

.confirmationButtons {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.confirmationButtons button {
  width: 45%;
  background-color: #11A6CE;
  border: none;
  padding: 1rem 2rem;
  text-align: center;
  text-decoration: none;
  color: #fff;
  cursor: pointer;
}



